.burger {
  @include burger;
  position: relative;
  z-index: 1000;
  display: none;
  // border: 1px solid $accent-color;
  width: 36px;
  height: 26px;

  @include tablet {
    display: block;
  }
  @include mobile {
    width: 30px;
    height: 20px;
  }
  &::before,
  &::after,
  &__line {
    // position: absolute;
    // top: 50%;
    // left: 50%;
    border-radius: 2px;
    // width: 26px;
    // height: 2px;
    background-color: $light-color;
    // transform: translate(-50%, -50%);

    // &:first-child {
    //   margin-top: -7px;
    // }
    // &:last-child {
    //   margin-top: 7px;
    // }
  }
}

.burger-close {
  position: absolute;
  display: none;
  top: 34px;
  right: $container-offset;
  // border: 1px solid $accent-color;
  width: 44px;
  height: 44px;
  background-image: url(../img/close.svg);
  background-position: center;
  background-size: 24px 24px;
  background-repeat: no-repeat;

  @include tablet {
    display: block;
  }
}
