.nav {

  @include tablet {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 300;
    overflow-y: auto;
    max-width: 400px;
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(25px);
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;

    .site-container--page-product & {
      background-color: $dark-color;
    }
  }
  @include mobile {
    max-width: none;
  }

  &__list {
    @include flex-v-center;

    @include tablet {
      flex-direction: column;
      align-items: start;
      padding: 124px 50px 50px;
    }
  }

  &__item {
    width: max-content;

    &:not(:last-child) {
      margin-right: $xl-offset;

      @include tablet {
        margin-right: 0;
      }
    }

    @include tablet {
      position: relative;
    }
    // &:hover ~ & {
    //   @include tablet {
    //     transform: translateY(10%);
    //     transition: transform .3s ease-in-out;
    //   }
    // }
    // &:hover > .submenu {
    //   opacity: 1;
    //   visibility: visible;
    //   transform: translateX(-10%) rotateX(0deg);
    //   transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;
    // }
    // &:hover > .nav__link--submenu::after {
    //     border-color: currentColor;
    // }
  }

  &__link {
    display: inline-block;
    padding: 12px 0;
    font-weight: 600;
    font-size: $main-size;
    color: inherit;
    transition: color .3s ease-in-out;

    @include tablet {
      padding: 16px 0;
      font-size: $second-size;
    }

    &--submenu {
      margin-right: 16px;
      @include tablet {
        margin-right: 0;
      }

      &::before {
        width: calc(100% + 14px + 16px);
      }
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        right: -16px;
        width: 8px;
        height: 8px;
        border-bottom: 2px solid $light-color;
        border-right: 2px solid $light-color;
        transform: translateY(-60%) rotate(45deg);
        transition: transform .3s ease-in-out;

        .nav__item:hover > & {
          border-color: currentColor;
        }


        @include tablet {
        border-bottom: 3px solid $light-color;
        border-right: 3px solid $light-color;
        transform: translateY(-50%) rotate(-45deg);
        transition: transform .3s ease-in-out;

        .nav__item:hover > & {
          transform: translateY(-50%) rotate(45deg);
          transition: transform .3s ease-in-out;
        }
        }
      }
    }
  }

}

.nav.menu--active {
  transform: none;
  opacity: 1;
  visibility: visible;
}
.nav__item:focus-within > .submenu {
  opacity: 1;
  visibility: visible;
}
