.submenu {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 35px;
  border-radius: 2px;
  // width: max-content;
  max-width: 300px;
  word-spacing: nowrap;
  color: $light-color;
  transform-origin: 0 0;
  transform: translateX(-10%) rotateX(-90deg);
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(25px);
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out, rotateX .3s ease-in-out;

  @include tablet {
    position: relative;
    display: none;
    padding: 0;
    padding-left: 16px;
    opacity: 1;
    visibility: visible;
    transform: rotateX(0deg);
    transform-origin: 0 0;
    // transform: rotateX(-90deg);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;
  }
  @keyframes animation {
    from {
      opacity: 0;
      transform: translate(15px);
    }
    to {
      opacity: 1;
      transform: translate(0px);
    }
  }
  .site-container--page-product & {
    background-color: $dark-color;
  }

  .nav__item:hover > & {
    opacity: 1;
    visibility: visible;
    display: block;
    transform: translateX(-10%) rotateX(0deg);
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;

    @include tablet {
      // position: relative;
      // opacity: 1;
      // visibility: visible;
      transform: rotateX(0deg);
      transition: opacity .3s ease-in-out, visibility .3s ease-in-out, transform .3s ease-in-out;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: $second-size;
    font-size: $main-size;
    color: inherit;
  }
}
